@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
/* colors */
.card__post:not(:last-of-type) {
  border-bottom: 1px solid purple; }

.card__post__content h2 {
  margin: 0 0 0.5rem 0; }

.card__post section {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 20px;
  width: 100%; }

.card__post__header {
  color: #303030;
  display: flex;
  align-items: center; }

.card__post__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 8px; }
  .card__post__icon img {
    height: 15px;
    width: 15px; }
  .card__post__icon strong {
    text-transform: capitalize;
    font-weight: bold;
    padding: 5px; }

.card__post__items {
  display: flex;
  justify-content: center; }
  .card__post__items p {
    font-size: 1.1rem;
    line-height: 200%; }
    .card__post__items p a {
      text-decoration: none;
      font-size: 1.2rem;
      color: #d1b873; }
      .card__post__items p a:hover {
        color: #484848; }

.card__post__image {
  border-radius: 4px;
  margin-left: 5px;
  width: 350px;
  height: 200px; }

.card__post__tags {
  display: flex;
  color: #303030;
  list-style: none;
  margin: 0;
  padding: 0;
  flex-wrap: wrap; }
  .card__post__tags li:first-child {
    padding-left: 5px; }
  .card__post__tags li a {
    background-color: #eee;
    border-radius: 3px 0 0 3px;
    color: #999;
    display: flex;
    align-items: center;
    height: 26px;
    padding: 0 20px 0 23px;
    position: relative;
    margin: 0 10px 10px 0;
    text-decoration: none;
    transition: color 0.2s;
    -webkit-transition: color 0.2s; }
    .card__post__tags li a:before {
      position: absolute;
      height: 6px;
      width: 6px;
      left: 10px;
      top: 10px;
      content: '';
      background: #fff;
      border-radius: 10px;
      box-shadow: inset 0 1px rgba(0, 0, 0, 0.25); }
    .card__post__tags li a:after {
      position: absolute;
      content: '';
      right: 0;
      top: 0;
      background: #fff;
      border-bottom: 13px solid transparent;
      border-left: 10px solid #eee;
      border-top: 13px solid transparent; }
    .card__post__tags li a:hover {
      background-color: crimson;
      color: white; }
    .card__post__tags li a:hover::after {
      border-left-color: crimson; }

@media (max-width: 768px) {
  .card__post__content {
    display: flex;
    flex-direction: column;
    width: 100%; }
  .card__post__icon {
    font-size: 0.6rem; }
  .card__post__items {
    display: flex;
    flex-direction: column-reverse; }
    .card__post__items img {
      width: 100%; } }

